.footer {
  background: linear-gradient(to bottom, #0056b3, #002c5b);
  padding: 40px;
  text-align: center;

  .footer-logo {
    margin-bottom: 20px;

    .footer-logo-img {
      width: 50px;
      height: 50px;
      margin-right: 10px;
      object-fit: cover;
    }

    .footer-logo-text {
      display: inline-block;
      font-size: 24px;
      font-weight: bold;
      vertical-align: middle;
    }
  }
  .footerContainerLinks {
    margin-bottom: 20px;

    a {
        color: #000000;
        background-color: rgb(255, 255, 67);
        font-weight: 700;
        text-decoration: none;
        cursor: pointer;
        margin: 0 10px;
        padding: 5px 10px;
        border-radius: 10px;
    }
  }

  .footerContainerLinksbottom{
    margin-bottom: 20px;

    a {
        color: #bbff00;
        font-weight: 700;
        text-decoration: none;
        cursor: pointer;
        margin: 0 10px;
        padding: 5px 10px;
        border-radius: 10px;
    }
  }

  .footerparas {
    margin-bottom: 20px;

    .footer-paragraph {
      margin-bottom: 15px;
      text-align: left;
    }

    a {
      color: #000000;
      background-color: rgb(255, 255, 67);
      font-weight: 700;
      text-decoration: none;
      cursor: pointer;
      margin: 0 10px;
      padding: 5px 10px;
      border-radius: 10px;
    }

    .footer-paragraph-Link {
      display: inline-block;
      color: #000000;
      background-color: rgb(255, 255, 67);
      font-weight: 700;
      cursor: pointer;
      margin: 0 10px;
      padding: 5px 10px;
      border-radius: 10px;
      svg {
        vertical-align: middle;
      }
    }
  }

  .footerContainerCopyrighttext {
    color: #888;
    font-size: 14px;
  }

  @media (max-width: 768px) {
    .footer-logo-img {
      width: 40px;
      height: 40px;
    }

    .footer-logo-text {
      font-size: 20px;
    }

    .footerparas {
      .footer-paragraph {
        font-size: 14px;
      }
    }
  }

  @media (max-width: 576px) {
    .footer {
      padding: 20px;
    }

    .footer-logo-img {
      width: 30px;
      height: 30px;
    }

    .footer-logo-text {
      font-size: 18px;
    }

    .footerparas {
      .footer-paragraph {
        font-size: 12px;
      }
    }
  }
}
