@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");
// Define variables for customization
$scrollbar-width: 5px;
$scrollbar-color: #ccc;
$thumb-color: #888;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  scrollbar-width: thin;
  scrollbar-color: $scrollbar-color transparent;
  background: linear-gradient(to right, #007bff, #0056b3);
  color: #fff;

  &::-webkit-scrollbar {
    width: $scrollbar-width;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $thumb-color;
    border-radius: 20px;
  }
}

/* =================================
======== homeUpperBody for telegram links and para ======= */

.homeUpperBody {
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  max-width: 1566px;
  width: 100%;
  margin: 0 auto;

  .homeUpperBodyHeading{
    span{
      color: rgb(0, 0, 0);
      border: 1px rgb(0, 0, 0) ridge;
      padding: 0 10px;
      border-radius: 20px;
      font-size: 18px;
      display: inline-block;
      transform: rotate(4deg);
    }
  }
  p {
    font-size: 18px;
    margin: 0;
  }
  .p {
    margin-bottom: 20px;
  }

  .telegramlink {
    display: inline-block;
    margin: 10px;
    padding: 12px 24px;
    font-size: 16px;
    color: #2b2b2b;
    background-color: #d9ff00;
    border-radius: 4px;
    text-decoration: none;
    cursor: pointer;

    svg {
      margin-right: 8px;
      vertical-align: middle;
    }

    &:hover {
      background-color: #a0da02;
    }
  }
}


