.HomePageContainer {
  max-width: 1566px;
  margin: 0 auto;

  .movieContentParent {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    justify-items: center;
    margin-top: 20px;
    width: 100%;

    .movieContentCard {
      padding: 10px;
      border-radius: 10px;
      background-color: #f9f9f9;
      box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
        rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
      transition: transform 0.3s ease;

      &:hover {
        transform: scale(1.05);
      }

      .movieContent {
        position: relative;
        width: 100%;
        height: 100%;

        .movieContentimg {
          width: 100%;
          height: 100%;
          max-width: 180px;
          max-height: 400px;
        }

        .movieContentTitle {
          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translate(-50%, -50%);
          text-align: center;
          width: 90%;
          font-size: 16px;
          font-weight: bold;
          margin: 0;
          color: #f9f9f9;
        }
      }
    }
  }

  .loadmoreContainer {
    margin: 25px auto;
    text-align: center;

    .loadmoreBtn {
      padding: 10px 20px;
      background-color: #bbff00;
      color: #000000;
      border: none;
      border-radius: 5px;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.3s ease;

      svg {
        margin-left: 5px;
        vertical-align: middle;
      }

      &:hover {
        background-color: rgba(187, 255, 0, 0.705);
      }
    }
  }

  @media (max-width: 768px) {
    .movieContentParent {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  @media (max-width: 480px) {
    .movieContentParent {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  @media (max-width: 300px) {
    .movieContentParent {
      grid-template-columns: 1fr;
    }
  }
}