.pnf {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    text-align: center;
    max-width: 1566px;
    margin: 20px auto;
    padding: 50px;
    box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
  }
  
  .pnf-title {
    font-size: 80px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .pnf-heading {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
  }
  
  .pnf-description {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .pnf-btn {
    display: inline-block;
    padding: 12px 24px;
    font-size: 18px;
    font-weight: 700;
    text-decoration: none;
    color: #333;
    background-color: #ffd900;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .pnf-btn:hover {
    background-color: #222;
  }
  