.CategoriesContainer {
  display: flex;
  justify-content: space-between;

  @media (max-width: 768px) {
    flex-direction: column;
    align-items: center;
  }
  .CategoriesContainerfilters {
    flex: 1;
    padding: 20px;

    @media (max-width: 768px) {
      flex: none;
    }

    .CategoriesContainerHeading {
      font-size: 24px;
      font-weight: bold;
      margin-bottom: 10px;
    }

    .CategoriesBox {
      display: flex;
      flex-wrap: wrap;


      .ant-checkbox-group {
        width: 100%;

        .ant-checkbox-group-item {
          margin-bottom: 10px;
        }
      }
    }

    .CategoriesResetParent {
      margin-top: 20px;
      text-align: center;

      .CategoriesResetBtn {
        padding: 10px 20px;
        background-color:  #0056b3;
        color: #fff;
        border: none;
        border-radius: 5px;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: #007bff;
        }
      }
    }

    @media (max-width: 768px) {
      padding: 10px;

      .CategoriesContainerHeading {
        font-size: 20px;
        margin-bottom: 5px;
      }
    }
  }

  .Categoriesmovies {
    flex: 5;
    padding: 20px;

    .CategoriesmoviesHeading {
      font-size: 32px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .CategoriesmoviesContent {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 20px;
      justify-items: center;

      .CategoriesmoviesCard {
        padding: 10px;
        border-radius: 10px;
        background-color: #f9f9f9;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;
        cursor: pointer;

        &:hover {
          transform: scale(1.05);
        }

        .movieContent {
          position: relative;
          width: 100%;
          height: 100%;

          .movieContentimg {
            width: 100%;
            height: 100%;
            max-width: 180px;
            max-height: 400px;
          }

          .CategoriesmoviesCardTitle {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            width: 90%;
            font-size: 16px;
            font-weight: bold;
            margin: 0;
            color: #f9f9f9;
          }
        }
      }
    }

    .CategoriesmoviesLoadmoreParent {
      margin-top: 20px auto;
      text-align: center;

      .CategoriesmoviesLoadmoreBtn {
        padding: 10px 20px;
        background-color: #bbff00;
        color: #000000;
        border: none;
        border-radius: 5px;
        font-size: 16px;
        font-weight: bold;
        cursor: pointer;
        transition: background-color 0.3s ease;

        svg {
          margin-left: 5px;
          vertical-align: middle;
        }

        &:hover {
          background-color: #0056b3;
        }
      }
    }

    @media (max-width: 768px) {
      padding: 10px;

      .CategoriesmoviesHeading {
        font-size: 24px;
        margin-bottom: 10px;
      }
    }
  }
}
