.categoryContainer {
  max-width: 1566px;
  margin: 0 auto;
  margin-top: 20px;

  .categoryContainerHeading {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }

  .categoryContainerSubHeading {
    font-size: 16px;
    margin-bottom: 10px;
  }

    .categoryContentCardParent {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      grid-gap: 20px;
      justify-items: center;

      .categoryContentCard {
        padding: 10px;
        border-radius: 5px;
        background-color: #f9f9f9;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;
        cursor: pointer;

        &:hover {
          transform: scale(1.05);
        }
        .categoryContentimgVideo {
          position: relative;
          width: 100%;
          height: 100%;

          .categoryContentCard-img{
            width: 100%;
            height: 100%;
            max-width: 200px;
            max-height: 400px;
          }
          .categoryContentCardTitle {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            width: 90%;
            font-size: 16px;
            font-weight: bold;
            margin: 0;
            color: #f9f9f9;
          }
        }
      }
    }

}
