.contact-page {
    padding: 50px;
    box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;
    max-width: 1566px;
    margin: 20px auto;

    .contact-title {
      font-size: 28px;
      font-weight: bold;
      margin-bottom: 20px;
    }
  
    .contact-info {
      font-size: 18px;
      margin-bottom: 20px;
    }
  
    .contact-details {
      display: flex;
      flex-direction: column;
      margin-bottom: 30px;
  
      .contact-item {
        display: flex;
        align-items: center;
        font-size: 16px;
        margin-bottom: 10px;
  
        svg {
          margin-right: 10px;
        }
      }
    }
  
    .contact-message {
      font-size: 16px;
      margin-bottom: 30px;
    }
  
    .contact-social {
      display: flex;
      align-items: center;
  
      .contact-social-item {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        margin-right: 20px;
        background-color: yellow;
        color: #333;
        padding: 5px 10px;
        font-weight: 600;
  
        svg {
          margin-left: 5px;
        }
      }
    }
  
    // Media Queries
    @media screen and (max-width: 768px) {
      .contact-page {
        padding: 30px;
      }
  
      .contact-title {
        font-size: 24px;
      }
  
      .contact-info {
        font-size: 16px;
      }
  
      .contact-details .contact-item {
        font-size: 14px;
      }
  
      .contact-message {
        font-size: 14px;
      }
  
      .contact-social .contact-social-item {
        font-size: 14px;
        flex-direction: column-reverse;
        svg {
            margin: 0;
          }
      }
    }
  }
  