.MoviesDetails {
  padding: 20px;

  .MovieDetailsContainer {
    width: 100%;
    max-width: 1566px;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;

    @media (max-width: 1024px) {
      flex-direction: column;
    }
    .MovieDetailsContainerCard {
      width: 50%;
      @media (max-width: 768px) {
        margin: 0 auto;
      }
      .MovieDetailsContainerCardimg {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .MovieDetails-info {
      width: 100%;
      margin-top: 20px;
      margin-left: 50px;

      @media (max-width: 1024px) {
        margin-left: 0;
      }

      .MovieDetailsTitle {
        font-size: 24px;
        font-weight: bold;
      }

      .MovieDetails-director,
      .MovieDetails-actors,
      .MovieDetails-country,
      .MovieDetails-postupdated,
      .MovieDetails-duration,
      .MovieDetails-release,
      .MovieDetailsDescription,
      .MovieDetailsCategory {
        font-size: 16px;
        color: #fff;
        text-transform: capitalize;

        span {
          font-weight: bold;
          color: #b4b4b4;
        }
      }
    }

    .MovieDownloadingLinks {
      .MovieDownloadinglinkHeading {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
        padding: 10px 0;
        background-color: yellowgreen;
        color: #000000;
        width: 100%;
        text-transform: capitalize;
      }
      .MovieDownloadinglink {
        display: inline-block;
        margin-right: 10px;
        margin-bottom: 10px;
        padding: 6px 12px;
        color: #fff;
        border: #fff ridge 1px;
        font-weight: 700;
        text-decoration: none;
        border-radius: 5px;
        transition: background-color 0.3s ease;
        cursor: pointer;

        &:hover {
          background-color: #ddd;
          color: #000000;
        }
      }

      .MovieDownloadinglink-disabled{
        color: red;
      }
    }
  }
  .serverButtonsBoss {
    .serverButtonsBossHeading {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      margin-bottom: 20px;
      padding: 10px 0;
      background-color: rgb(174, 255, 0);
      color: #1b1818;
      width: 84%;
      text-transform: capitalize;
      font-weight: 700;
    }
    p {
      text-align: center;
    }
    .serverButtons {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;

      button {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 10px;
        padding: 6px 12px;
        background-color: #f9f9f9;
        color: #000000;
        border-radius: 5px;
        border: none;
        cursor: pointer;
        transition: background-color 0.3s ease;
        svg {
          margin-right: 5px;
        }
        &:hover {
          background-color: #ddd;
        }
      }
    }
  }

  .MovieOnlineWatchingLinks {
    .loading-container {
      display: flex;
      align-items: center;
      justify-content: center;

      p {
        font-size: 20px;
        font-weight: 700;
        color: #fff;
      }
    }
    iframe {
      width: 100%;
      height: 100vh;
      margin-bottom: 20px;
      border: 1px ridge gray;
      overflow: hidden;

      @media (max-width: 1024px) {
        height: 50vh;
      }
      @media (max-width: 576px) {
        height: 30vh;
      }
    }
  }

  .similarProducts {
    margin: 0 auto;
    margin-top: 20px;

    .similarProductsHeading {
      font-size: 24px;
      font-weight: bold;
    }

    .similarProductsSubHeading {
      font-size: 16px;
      color: #999;
    }

    .MoviecontentcardParent {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      grid-gap: 20px;
      justify-items: center;

      .Moviecontentcard {
        padding: 10px;
        border-radius: 5px;
        background-color: #f9f9f9;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
          rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
        transition: transform 0.3s ease;
        cursor: pointer;

        &:hover {
          transform: scale(1.05);
        }
        .Moviecontent {
          position: relative;
          width: 100%;
          height: 100%;

          .Moviecontentcardimg {
            width: 100%;
            height: 100%;
            max-width: 200px;
            max-height: 400px;
          }
          .MoviecontentcardTitle {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            width: 90%;
            font-size: 16px;
            font-weight: bold;
            margin: 0;
            color: #f9f9f9;
          }
        }
      }
    }

    .HomeText {
      margin-top: 25px;

      a {
        text-decoration: none;
        border: 1px ridge #fff;
        color: #fff;
        font-size: 22px;
        font-weight: 700;
        padding: 0 5px;
        border-radius: 20px;
        display: inline-block;
        margin-right: 10px;
        margin-bottom: 10px;
        padding: 6px 12px;
        color: #fff;
        border: #fff ridge 1px;
        font-weight: 700;
        text-decoration: none;
        border-radius: 5px;
        transition: background-color 0.3s ease;
        cursor: pointer;
        text-transform: uppercase;

        &:hover {
          background-color: #3b1e1e;
          color: #ffffff;
        }
      }
    }
  }
}
