.privacy-policy {
  max-width: 1566px;
  margin: 20px auto;
  padding: 50px;
  box-shadow: rgba(67, 71, 85, 0.27) 0px 0px 0.25em, rgba(90, 125, 188, 0.05) 0px 0.25em 1em;

  .privacy-policy-title {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 30px;
  }

  .privacy-policy-paragraph {
    font-size: 16px;
    line-height: 1.6;
    margin-bottom: 20px;
  }

  .privacy-policy-paragraph:last-child {
    margin-bottom: 0;
  }
}
